.main{
    width: calc(100% - 32px);
    display: flex;
    flex-direction: column;
    padding: 16px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
    margin-bottom: 16px;
}
.main a{
    text-decoration: none;
}
.activityHeader{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.program{
    color: #757575;
    font-size: 12px;
    font-weight: 500;
}
.headerSide{
    display: flex;
    align-items: center;
}
.status{
    color: #3d3d3d;
    font-size: 12px;
    padding: 5px 10px;
    border-radius: 17.5px;
    font-weight: 600;
    background-color: rgba(253, 200, 11, 0.25);
}
.timeBlock{
    display: flex;
    align-items: center;
    margin-left: 16px;
}
.timeBlock svg path{
    fill: black;
}
.time{
    color: #757575;
    font-size: 13px;
    font-weight: 700;  
    margin-left: 4px;
}
.main h3{
    margin-block-end: 18px;
    margin-block-start: 14px;
    color: #3d3d3d;
    font-size: 18px;
}
.progressBlock{
    display: flex;
    align-items: center;
}
.progressBlock span{
    font-size: 12px;
    color: #6e798c;
    font-weight: 700;
    margin-left: 14px;
}

@media screen and (max-width: 360px){
    .headerSide{
        align-items: flex-end;
        flex-direction: column-reverse;
    }
    .status{
        margin-top: 8px;
    }
}